<template>
  <div class="admin-setting">
    <search @searchList="searchList" />
    <admin-list ref="adminList" @showAddAdmin="addAdminShow = true" />
    <tip
      v-if="addAdminShow"
      v-model="addAdminShow"
      btn-text="确定"
      :has-footer="false"
    >
      <section slot="content" class="center">
        <el-form
          ref="ruleForm"
          :model="ruleAdmin"
          :rules="rules"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="选择管理员" prop="admin">
            <el-select v-model="ruleAdmin.admin" placeholder="请选择">
              <el-option
                v-for="(item, index) of userList"
                :key="index"
                :label="item.name"
                :value="item._id"
              />
            </el-select>
          </el-form-item>
          <el-form-item label="选择角色" prop="role">
            <el-select v-model="ruleAdmin.role" placeholder="请选择">
              <el-option label="人事" value="human_resource" />
              <el-option label="医生" value="doctor" />
              <el-option label="培训师" value="teacher" />
            </el-select>
          </el-form-item>
          <el-form-item class="sure">
            <el-button type="primary" @click="submitForm('ruleForm')"
              >确定</el-button
            >
          </el-form-item>
        </el-form>
      </section>
    </tip>
  </div>
</template>

<script>
import Search from './components/Search'
import AdminList from './components/AdminList'
import Tip from '@/components/Dialog'
import { userAll, addAdmin } from '@/api/learningAdmin'
export default {
  components: {
    Search,
    AdminList,
    Tip
  },
  data() {
    return {
      addAdminShow: false,
      userList: [],
      adminList: [],
      ruleAdmin: {
        admin: '',
        role: ''
      },
      rules: {
        admin: [{ required: true, message: '请选择管理员', trigger: 'change' }],
        role: [{ required: true, message: '请选择角色', trigger: 'change' }]
      }
    }
  },
  watch: {
    addAdminShow() {
      if (!this.addAdminShow) {
        this.ruleAdmin.admin = ''
        this.ruleAdmin.role = ''
      }
    }
  },
  async mounted() {
    const { data } = await userAll()
    if (data && data.length) this.userList = data
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          const { code, data } = await addAdmin({
            admin: this.ruleAdmin.admin,
            type: this.ruleAdmin.role
          })
          if (code === 20000) {
            if (data.result) {
              this.$message({ message: '添加成功', type: 'success' })
              await this.$refs['adminList'].getList(1)
            } else {
              return this.$message({ message: data.message, type: 'error' })
            }
          }
          this.addAdminShow = false
        } else {
          return false
        }
      })
    },
    searchList(name) {
      this.$refs['adminList'].getList(1, name)
    }
  }
}
</script>

<style lang="scss" scoped>
.admin-setting {
  .sure {
    /deep/.el-form-item__content {
      margin-left: 0 !important;
    }
  }
  /deep/input,
  /deep/button {
    height: 32px;
  }
  /deep/button {
    padding: 6px 20px;
    &.el-dialog__headerbtn {
      top: 10px;
      right: 5px;
    }
  }
}
</style>
