<template>
  <div class="admin-list">
    <div class="title">
      <div>管理员列表</div>
      <el-button type="primary" @click="showAdd">选择管理员</el-button>
    </div>
    <el-table :data="list" style="width: 100%" :empty-text="hasListInfo">
      <el-table-column
        fixed
        prop="num"
        label="序号"
        min-width="150"
        align="center"
      />
      <el-table-column
        prop="name"
        label="姓名"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="type"
        label="角色"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="mobile"
        label="手机号"
        min-width="120"
        align="center"
      />
      <el-table-column
        label="操作"
        min-width="150"
        align="center"
        fixed="right"
      >
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="deleteClick(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        :current-page="currentPage"
        :page-sizes="[8, 10, 15, 20]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
    <el-dialog
      title="删除提示"
      :visible.sync="delShow"
      width="30%"
      center
      top="30vh"
    >
      <p class="center">删除后，该管理员信息将不再保留，请确认是否删除？</p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delShow = false">取 消</el-button>
        <el-button type="primary" @click="deleteAdmin">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { adminList, deleteAdmin } from '@/api/learningAdmin'
export default {
  data() {
    return {
      list: [],
      currentPage: 1,
      delShow: false,
      pageSize: 8,
      count: 0,
      delId: '',
      hasListInfo: '暂无数据'
    }
  },
  created() {
    this.getList(1)
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val
      this.getList(1)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getList(val)
    },
    deleteClick(row) {
      this.delShow = true
      this.delId = row.id
    },
    async deleteAdmin() {
      const { data } = await deleteAdmin(this.delId)
      if (data && data.result) {
        this.$message({
          message: '删除成功',
          type: 'success'
        })
      }
      await this.getList(this.currentPage)
      this.delShow = false
    },
    showAdd() {
      this.$emit('showAddAdmin')
    },
    async getList(page, name) {
      if (name) this.hasListInfo = '暂无数据，请重新输入关键字查询'
      else this.hasListInfo = '暂无数据'
      const info = {
        pageSize: this.pageSize,
        page: page,
        name: name || undefined
      }
      const { data } = await adminList(info)
      if (data) {
        if (data.result) {
          this.list = data.list.map((item, index) => {
            return {
              num: index + 1,
              name: item.name,
              type:
                item.type === 'human_resource'
                  ? '人事'
                  : item.type === 'teacher'
                  ? '培训师'
                  : '医生',
              mobile: item.mobile,
              id: item._id
            }
          })
        } else this.list = data.list
        this.count = data.count
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.admin-list {
  margin: 20px 0;
  padding: 20px;
  border: 1px solid #ebeef5;
  .pagination {
    margin-top: 50px;
    text-align: center;
  }
  .title {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;
    line-height: 32px;
    font-size: 20px;
    font-weight: bold;
    color: #333;
  }
  /deep/button {
    padding: 6px 20px;
    height: 32px;
  }
  /deep/.el-dialog__headerbtn {
    top: 10px;
    right: 5px;
  }
}
</style>
