<template>
  <div class="admin-search">
    <el-form :inline="true" :model="users" class="demo-form-inline">
      <el-form-item>
        <el-input v-model="users.name" clearable placeholder="请输入姓名" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      users: {
        name: ''
      }
    }
  },
  methods: {
    onSubmit() {
      this.$emit('searchList', this.users.name)
    }
  }
}
</script>

<style lang="scss" scoped>
.admin-search {
  /deep/.el-form-item {
    margin-right: 30px;
    input {
      width: 220px;
      height: 32px;
    }
    button {
      padding: 6px 20px;
      height: 32px;
    }
  }
